/**
 * Login page smoke test: calls createServerSupabaseClient, getSession(),
 * and renders the result as JSON to confirm Supabase connection. Replace
 * with the real login UI in the next task.
 */

import { createServerSupabaseClient } from "@/lib/supabase"
import { cn } from "@/lib/utils"

export default async function LoginPage(): Promise<JSX.Element> {
  const supabase = await createServerSupabaseClient()
  const { data, error } = await supabase.auth.getSession()

  return (
    <main
      className={cn(
        "min-h-screen bg-surface px-4 py-8 flex flex-col items-center justify-center"
      )}
    >
      <h1 className="font-display text-2xl font-semibold text-text-primary mb-4">
        Login (smoke test)
      </h1>
      <pre className="text-left text-sm text-text-secondary bg-surface-card p-4 rounded-lg overflow-auto max-w-full max-h-[60vh] font-mono">
        {error != null
          ? JSON.stringify({ error: error.message }, null, 2)
          : JSON.stringify({ session: data.session, user: data.session?.user }, null, 2)}
      </pre>
    </main>
  )
}
